    /* ///////////////////////////////////////////////////
    
    HARDCOVER
    Table of Contents
    
    1. container
    2. background & color
    3. opening cover, back cover and pages
    4. position, transform y transition
    5. events
    6. Bonus
    - Cover design
    - Ribbon
